* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	// font-family: consolas;
}

body {
	min-height: 100vh;
	// background: linear-gradient(0deg, #fff1eb 0, #ace0f9);
}

.container {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 40px 0;
	flex-wrap: wrap;
	// background-color: rgb(142, 142, 197);
}

.container .box {
	position: relative;
	width: 280px;
	height: 300px;
	color: #fff;
	/* background: linear-gradient(0deg, #9795f0 0, #fbc8d4); */
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 20px 30px;
	transition: 0.5s;
	/* background-image: linear-gradient(to top, #9795f0 0%, #fbc8d4 100%); */
	/* border: solid; */
	/* border-color: white; */
	box-shadow: #9795f0;
}

.container .box:hover {
	background: rgba(0, 0, 0, 0.2);
	transform: translateY(-20px);
	border: solid;
	border-color: rgb(239, 239, 245);
	border-width: 1px;
}

.container .box::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	/* background: linear-gradient(45deg, #4facfe, #00f2fe); */
}

.container .box::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	/* background: linear-gradient(45deg, #4facfe, #00f2fe); */
	/* filter: blur(5px); */
}
