.container .box span {
	position: absolute;
	top: 6px;
	left: 6px;
	right: 6px;
	bottom: 6px;
	/* background: rgba(0, 0, 0, 0.35); */
	z-index: 2;
}

.container .box span::before {
	content: "";
	position: absolute;
	top: 0px;
	left: 0px;
	width: 50%;
	height: 100%;
	/* background: rgba(0, 0, 0, 0.6); */
	pointer-events: none;
}

.container .box .content {
	position: relative;
	z-index: 3;
	padding: 20px 40px;
}

.uphoto {
	top: 5px;
	margin-top: 2%;
	font-size: 2em;
	color: #fff;
	margin-bottom: 10px;
	text-decoration: none;
	width: 150px;
	height: 150px;
	border-radius: 100%;
	color: black;
	border-width: 3px;
	border-color: #fff;
	border-style: solid;
}

.container .box .content a img {
	max-width: 25px;
	max-height: 25px;
	margin-left: 10px;
	margin-right: 10px; // display: flex;
	// justify-content: space-between;
	/* border: solid; */
	/* border-color: rgb(240, 234, 245);
    border-width: 1px; */
}

.container .box .content {
	color: black;
	font-size: 150%;
}

.heading {
	display: flex;
	justify-content: center;
	justify-content: space-around;
	padding-top: 20px;
	padding-bottom: 20px;
}

hr {
	border-top: 1px solid #0be4ef;
}

hr.hr1 {
	width: 25%;
	margin-left: 37.5%;
	margin-right: 37.5%;
}

hr.hr2 {
	width: 50%;
	margin-left: 25%;
	margin-right: 25%;
}

.modal_main {
	width: 960px;
	background-color: black;
}

.modal-body {
	font-family: "Jura";
}

.modal-dialog {
	max-width: 100vw;
	margin-left: 10vw;
	margin-right: 10vw;
	position: relative;
	z-index: 4000;
}

.close {
	display: block;
	color: black;
}

.close > span {
	display: block;
	color: white;
	font-size: 150%;
}

.modal-content {
	background-color: #1b77a5;
	// border-radius: 03%;
	border-radius: 0%;
}

.heading {
	color: #fff;
}

.modal-header {
	border-bottom: none;
}

p {
	color: white;
	// font-size: 150%;
}

.content > center {
	width: 200px;
	height: 279px;
}
