.Base {
	font: 900 50px/1 "Source Sans Pro", Arial, sans-serif;
	text-transform: uppercase;
	color: #fff;
	background-color: #92b5f9;
	text-align: center;
	letter-spacing: -3px;
	width: 100%;
	display: grid;
	place-items: center;
	grid-template-columns: auto auto;
	z-index: 80;
	grid-template-areas: " r1 r2 ";
	//	background-color: aqua;
}

.vid {
	width: calc((100vw - 50px) / 2);
	height: auto;
	z-index: 100;
	grid-area: r1;
	max-height: 100vh;
}

#myVideo {
	position: relative;
	// left: 50px;
	// top: 0;
	// width: calc((100vw - 50px) / 2);
	// left: 3%;
	// width: 100%;
	height: auto;
	//	background: rgba(0, 0, 0, 1);
	//	opacity: 0.5;
	width: calc((100vw - 50px) / 2);
	height: auto;
	// left: calc(-150 * ((100vw - 50px) / 2) / 200);
	max-height: 100vh;
	z-index: +1;
}

.back1 {
	display: none;
	z-index: 300;
	position: absolute;
	background-color: #92b5f9;
	height: 100%;
	left: 50px;
	width: calc((((100vw - 50px) / 2) - 495px) / 1.1);
}

.back2 {
	display: none;
	z-index: 300;
	position: absolute;
	background-color: #92b5f9;
	height: 100%;
	top: 0px;
	left: calc(50px + ((((100vw - 50px) / 2) - 495px) / 2.5) + 495px);
	width: calc((((100vw - 50px) / 2) - 495px) / 1.1);
}

.cnt {
	//	min-width: calc((100vw - 50px) / 2);
	width: calc((100vw - 50px) / 2);
	height: 100%;
	max-height: 100vh;
	background-color: #92b5f9;
	z-index: 200;
	display: grid;
	place-items: center;
	grid-area: r2;
}

.cntcnt {
	width: calc((100vw - 50px) / 2);
}

#Hacklogo {
	position: relative;
	width: 100%;
	height: auto;
}

