#typo {
	position: relative;
	font: 900 50px/1 "Source Sans Pro", Arial, sans-serif;
	text-transform: uppercase;
	color: #fff;
	// background-color: #92b5f9;
	text-align: center;
	letter-spacing: -3px;
	// padding-top: 8%;
	width: 100%;
}

.hackl {
	width: 80%;
}

@media only screen and (max-width: 960px) {
	.Base {
		grid-template-columns: 1fr;
		grid-template-rows: auto;
		grid-template-areas: " r1 ";
	}
	.vid {
		width: 100%;
		grid-area: r1;
	}
	#myVideo {
		width: 100%;
		//		left: calc(-150 * (100%) / 200);
	}
	.cnt {
		position: absolute;
		height: 100%;
		width: calc((100vw - 50px));
		grid-area: r1;
		background-color: #92b5f900;
	}
	.cntcnt {
		width: calc((100vw - 50px));
		display: grid;
		place-items: center;
		height: 100%;
	}
	.h {
		font: 900 50px/1 "Source Sans Pro", Arial, sans-serif;
		padding-top: 1%;
	}
	#typo {
		font: 900 30px/1 "Source Sans Pro", Arial, sans-serif;
		position: absolute;
		bottom: 20px;
	}
	#Hacklogo {
		position: absolute;
		top: 10px;
	}
	.hackl {
		width: 45%;
	}
	.noMob {
		display: none;
	}
}

