@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,200,200italic,300,300italic,900,700italic,700,600italic,600,400italic);
.time {
    font-family: 'Titillium Web', cursive;
    // width: 800px;
    margin: 0 auto;
    color: white;
    background: rgb(30, 17, 59);
    font-weight: 100;
}

.times {
    text-align: center;
    display: inline-block;
    line-height: 1;
    padding: 20px;
    font-size: 40px;
}

span {
    display: block;
    font-size: 20px;
    color: white;
}

#days {
    font-size: 100px;
    color: #db4844;
}

#hours {
    font-size: 100px;
    color: #f07c22;
}

#minutes {
    font-size: 100px;
    color: #f6da74;
}

#seconds {
    font-size: 50px;
    color: #abcd58;
}

.socials {
    padding: 20px;
    bottom: 0;
}

.left {
    float: left;
}

.right {
    float: right;
}

.status {
    padding-top: 10%;
}

.social_img {
    width: 2rem;
    padding: 3px;
}