body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: gentona;
  src: local('gentona'), url("/static/media/Gentona Book.ffcd17af.otf") format('opentype');
}

@font-face {
  font-family: gentona;
  src: url("/static/media/Gentona Book Bold.37513f31.otf");
  font-weight: bold;
}

@font-face {
  font-family: futura;
  src: local('futura'), url("/static/media/Futura Book font.ed19af44.ttf") format('truetype');
}

@font-face {
  font-family: kgSummerSunShine;
  src: local('kgSummerSunShine'), url(/static/media/KGSummerSunshine.cb8f5087.ttf) format('truetype');
}

@font-face {
  font-family: kgSummerSunShineShadow;
  src: local('kgSummerSunShineShadow'), url(/static/media/KGSummerSunshineShadow.aed00ff2.ttf) format('truetype');
}

@font-face {
  font-family: kgSummerSunShineBlackout;
  src: local('kgSummerSunShineBlackout'), url(/static/media/KGSummerSunshineBlackout.553fc69f.ttf) format('truetype');
}
.App {
	text-align: center;
}

* {
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, Helvetica, sans-serif;
	line-height: 1.4;
}

a {
	text-decoration: none;
	color: #333;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
	overflow-x: hidden;
	display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}


